
import React, { useEffect, useState } from "react";
import {keys} from './map-data';
import { ProTable } from '@ant-design/pro-components';
import { DownloadOutlined } from '@ant-design/icons';
import {exportToExcel} from './excel';
import { Button, Form, Input, Row, Col, Select, Space, Card, Flex } from 'antd';
const { Option } = Select;




const Region = (props: any) => {
    let { getQueryParams,getProCity } = props;
    let [data, setData] = useState<any>([]);
    let [province, setProvince] = useState<any>([]);
    let [city, setCity] = useState<any>([]);
    let [proCity, setProCity] = useState<any>([])



    const fetchData = async (keys: string) => {
        await fetch(`https://restapi.amap.com/v3/config/district?key=${keys}&subdistrict=2`)
            .then((res) => res.json())
            .then((results) => {
                let areaArr = results?.districts[0].districts || [];
                let proArr = areaArr.map(({ adcode = '', name = '' }) => ({ adcode, name }))
                setData(areaArr)
                setProvince(proArr)
            });
    };

    useEffect(() => {
        fetchData(keys[0])
        console.log('页面初始化')
    }, [])

    let areaPro = ['北京市', '天津市', '上海市', '重庆市', '香港特别行政区', '澳门特别行政区', '台湾省']
    let cityListArr: any = []
    let proCityArr: any = []
    useEffect(() => {
        cityListArr = []
        if (!data.length) return;
        let proArr = data.map(({ name = '' }) => name).filter((item: string) => !areaPro.includes(item))
        proArr.map((item: string) => cityList(item))
        data.map(({ name = '' }) => name).map((item: string) => proCityList(item))
        setProCity(proCityArr)
        cityListArr = [ '北京市', '天津市', '上海市', '重庆市',...cityListArr]
        console.log('子集省市数据',proCity)

    }, [data])

    const cityList = (value: string) => {
        const cityArr = data.filter((item: any) => item.name === value)[0]
        const lastCityArr = (cityArr.districts || []).map(({ name = '' }) => name)
        cityListArr = [...cityListArr, ...lastCityArr]
        console.log(cityListArr)
    };

    // 省市数据
    const proCityList = (value: string) => {
        const arr = data.filter((item: any) => item.name === value)[0]
        const lastProCityArr = (arr?.districts || []).map(({ name = '' ,pname=''}) => {return {'省份':value,'城市':name}})
        proCityArr=[...proCityArr,...lastProCityArr]
        console.log('省市数据',proCityArr)
    };


    const onFinish = (values: any) => {
        let { keys = '', keywords = '', city = '', province = '' } = values;
        let params = { keys, keywords, region: city || province }
        getQueryParams(params)
        console.log(values);
    };
    const onReset = () => {
        form.resetFields();
    };


    const onProvinceChange = (value: string) => {
        const cityArr = data.filter((item: any) => item.name === value)[0]
        const lastCityArr = (cityArr.districts || []).map(({ adcode = '', name = '' }) => ({ adcode, name }))
        setCity(lastCityArr)
    };




    const [form] = Form.useForm();
    const formLayout = <Form
        form={form}
        name="control-hooks"
        onFinish={onFinish}

    >
        <Row gutter={[24, 12]}>
            <Col span={8}>
                <Form.Item name="keys" label="Keys" rules={[{ required: true }]} initialValue={keys[0]}>
                    <Select
                        placeholder="请选择"
                        allowClear
                    >
                        {keys.map(item => <Option key={item} value={item}>{item}</Option>)}
                    </Select>
                </Form.Item>
            </Col>
            <Col span={8}>
                <Form.Item name="keywords" label="keywords" rules={[{ required: true }]} initialValue={'途虎'}>
                    <Input />
                </Form.Item>
            </Col>
        </Row>
        <Row gutter={[24, 12]}>
            <Col span={8}>
                <Form.Item name="province" label="省级" rules={[{ required: true }]}>
                    <Select
                        placeholder="请选择"
                        onChange={onProvinceChange}
                        allowClear
                    >
                        {province.map(({ adcode, name }: any) => <Option key={adcode} value={name}>{name}</Option>)}
                    </Select>
                </Form.Item>
            </Col>
            <Col span={8}>
                <Form.Item name="city" label="市级" rules={[{ required: true }]}>
                    <Select
                        placeholder="请选择"
                        allowClear
                    >
                        {city.map(({ adcode, name }: any) => <Option key={adcode} value={name}>{name}</Option>)}
                    </Select>
                </Form.Item>
            </Col>
            <Col span={8}>
                <Form.Item>
                    <Space>
                        <Button type="primary" htmlType="submit">
                            查询
                        </Button>
                        <Button htmlType="button" onClick={onReset} icon={<DownloadOutlined />} disabled={true}>
                            导出数据
                        </Button>
                        <Button onClick={()=>exportToExcel(proCity,'省市')}  icon={<DownloadOutlined />} >导出省份数据</Button>
                    </Space>
                </Form.Item>
            </Col>
        </Row>
    </Form>


    return (
        <div>
            <Card> {formLayout}
                {/* <Flex gap="large" justify="flex-end">
                    <Button htmlType="button" onClick={onReset} icon={<DownloadOutlined />}>
                        批量导出
                    </Button>
                </Flex> */}
            </Card>
          

        </div>
    );
}
export default Region;