import React, { useEffect, useState } from "react";
import { Alert, message } from "antd";
import { cityList } from './map-data';
import {exportToExcel} from './excel';

const Info = (props: any) => {
  let { queryParams } = props
  let { keys = '', keywords = '', region = '' } = queryParams;
  const [params, setParams] = useState({ keys: '', keywords: '' });
  let [tableParams, setTableParams] = useState<any>([]);
  let [proCity, setProCity] = useState<any>([]);
  let [city, setCity] = useState([...cityList])
  useEffect(() => {
    setParams({ keys, keywords })
    // setCity([region])
  }, [queryParams])
  
  let allList: any = [];
  let load = true;
  const fetchData = async (c: string, page = 1) => {
    if (!load) return;
    // await fetch(`https://restapi.amap.com/v3/place/text?key=${params?.keys}&keywords=${params?.keywords}&region=${c}&offset=${25}&page=${page}`)
    await fetch(`https://restapi.amap.com/v3/place/text?key=6ea7b8f05aec45cdc0708ceb4994d878&keywords=途虎&region=${c}&offset=${25}&page=${page}`)
      .then((res) => res.json())
      .then((results) => {
        let map: any = results.pois;
        let list: any = [];
        for (let { pname, cityname, name, address, location } of map) {
          // list = [...list, { 当前省份: pname, 当前城市: cityname, 名称: name, 地址: address, 经纬度坐标: location }]
          list = [...list, { 当前省份: pname, 当前城市: cityname }]
        }
        console.log(list);
        console.log(results);
        console.log('页码' + page + '总页数' + Math.ceil(results.count / 25) + '总条数' + allList.length + '已存条数' + tableParams.length + '城市' + c);
        allList = [...allList, ...list];
        if (page <= Math.ceil(results.count / 25)) {
          return fetchData(c, ++page)
        }
        else {
          setTableParams([...allList])
          return console.log('一个城市数据查找结束');
        }
        load = true
      }).catch(error => {
        load = false
      });
  };


  const getCity = async () => {
    for (let i = 0; i < city.length; i++) {
      console.log('函数中城市数据' + city[i])
      if(!load) return  message.warning('调用配额已耗尽,请切换keys');
      await fetchData(city[i], 1)
    }
  }


  return (
    <div>

      <button onClick={getCity}>获取数据</button>
      <button onClick={()=>exportToExcel(tableParams,'门店')}>导出数据</button>

    </div>
  );
}
export default Info;